import * as React from 'react';
import { Adsense } from '@client/services/service-proxies';
const Fragment = React.Fragment;

interface Props {
  adsense: Adsense[];
}
interface State {}
export class RightSideBar extends React.Component<Props, State> {
  render () {
    const adsPosition4 = this.props.adsense.filter((item) => item.position === 4)[0];
    const adsPosition5 = this.props.adsense.filter((item) => item.position === 5)[0];

    return (
      <Fragment>
        <a href={adsPosition4 && adsPosition4.hyperlink ? adsPosition4.hyperlink : '#'}>
          <img
            src={adsPosition4 && adsPosition4.imageUrl ? adsPosition4.imageUrl : '/static/images/sidebar-1.png'}
            width='100%'
          />
        </a>

        <a href={adsPosition5 && adsPosition5.hyperlink ? adsPosition5.hyperlink : '#'}>
          <img
            src={adsPosition5 && adsPosition5.imageUrl ? adsPosition5.imageUrl : '/static/images/sidebar-1.png'}
            width='100%'
            style={{marginTop: '30px'}}
          />
        </a>
      </Fragment>
    );
  }
}
